:root{
    --background : #211D25;
    --letter-grid : #343037;
    --button : #583D74;
}

body{
    height : 100vh;
    background-color: var(--background);
    display: flex;
    justify-content: center;
    align-items: center;
}

#root{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.letters{
    background-color: var(--letter-grid);
    width : 1000px;
    height : 60px;
    padding : 15px;
    text-align: center;
    display:flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    box-shadow: -6px -6px 6px rgb(26, 26, 26, 0.8),
                6px 6px 6px rgb(26, 26, 26, 0.8);
    margin-bottom: 50px;
}

span{
    padding : 10px;
    display: inline-block;
    border-bottom: 1px solid rgb(107, 107, 107);
    width : 20px;
    height : 20px;
    color : white;
    font-size: 20px;
}

#left{
    color : #ddd;
    margin-bottom: 30px;
}

.click{
    display : none;
}

.button.guessed{
    background-color: beige;
    pointer-events: none;
}

.buttons{
    width : 90%;
    display : flex;
    flex-wrap: wrap;
    justify-content: center;
    transition: 0.5s;
}

.buttons.click{
    opacity: 0;
}

.buttons.clear{
    opacity: 0;
}

.button{
    background-color: #5c4a5a;
    color :white;
    margin : 2%;
    display : inline-block;
    border : 1px solid rgba(0,0,0,0.21);
    border-bottom-color : rgva(0,0,0,0.34);
    text-shadow : 0 1px rgba(0,0,0,0.15);
    box-shadow: 0 1px 0 rgba(255,255,255,0.34) inset, 
                0 2px 0 -1px rgba(0,0,0,0.13), 
                0 3px 0 -1px rgba(0,0,0,0.08), 
                0 3px 13px -1px rgba(0,0,0,0.21);
    padding : 15px;
    width : 50px;
    height : 50px;
    border-radius: 10px;    
    transition: 0.25s;
}

.button:hover{
    background-color: #362935;
}

.button.guessed{
    color :white;
    background-color: #580f50;
    box-shadow: inset 5px 5px 3px 3px rgb(32, 32, 32, 0.3);
}

.waiting{
    color : white;
    font-size: 42px;
    font-style: italic;
}

#resetBtn{
    background-color: transparent;
    color :white;
    margin : 2%;
    border : none;
    display : inline-block;
    padding : 15px;
    width : 100px;
    height : 50px;
    border-radius: 10px;    
    transition: .8s;
}

#resetBtn:hover{
    border-bottom-color : rgba(0,0,0,0.34);
    text-shadow : 0 1px rgba(0,0,0,0.15);
    box-shadow: 0 1px 0 rgba(255,255,255,0.34) inset, 
                0 2px 0 -1px rgba(0,0,0,0.13), 
                0 3px 0 -1px rgba(0,0,0,0.08), 
                0 3px 13px -1px rgba(0,0,0,0.21);
    padding : 15px;
    width : 100px;
    height : 50px;
    border-radius: 10px;    
}